import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { StoreLedgerReportComponent } from './store-ledger-report.component';

const routes: Routes = [
    {
        path: '', component: StoreLedgerReportComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class StoreLedgerReportRoutingModule { }



