import { NgModule,NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { StoreLedgerReportRoutingModule } from './store-ledger-report-routing.module';
import { StoreLedgerReportComponent } from './store-ledger-report.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    StoreLedgerReportRoutingModule
  ],
  declarations: [
    StoreLedgerReportComponent
  ],
  schemas:[
    NO_ERRORS_SCHEMA
  ]
})
export class StoreLedgerReportModule { }



