import { Component, OnInit, ViewChild, ElementRef, ViewContainerRef, ComponentFactoryResolver, Inject } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { routerTransition } from '../../../router.animations';
import { QuickaccessComponent, AllReportService, FileExportService, CurrentUserService } from '../../../shared';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';

@Component({
  selector: 'app-store-ledger-report',
  templateUrl: './store-ledger-report.component.html',
  styleUrls: ['./store-ledger-report.component.scss']
})
export class StoreLedgerReportComponent implements OnInit {


  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('quickAccess', { read: ViewContainerRef }) quickAccess: ViewContainerRef;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;

  storeLedgerReportForm: FormGroup;
  @ViewChild('sComboBox') sComboBox: jqxComboBoxComponent;
  storeAdapter: any = [];
  store_data: any = [];
  private apiUrl: string;
  userData: any = {};
  private stockId = 0;
  reportDatas: any;
  showGenerateReportButton: boolean = false;

  constructor(
    private fb: FormBuilder,
    private rs: AllReportService,
    private fes: FileExportService,
    private cus: CurrentUserService,
    private componentFactoryResolver: ComponentFactoryResolver,
    @Inject('API_URL') apiUrl: string
  ) {
    this.apiUrl = apiUrl;
    this.userData['token'] = this.cus.getTokenData()['token'];
    this.createForm();
  }

  ngOnInit() {
    this.createQuickAccess();
    this.store_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'store?limit=all',
      };

    this.storeAdapter = new jqx.dataAdapter(this.store_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = [];
        let dt = { };
        dt['id'] = '';
        dt['name'] = 'Select All';
        tData.push(dt);
        for(let i=0; i<data.data.length; i++){
          tData.push(data.data[i]);
        }
        console.info(tData);
        return tData;
      }
    });
  }

  createQuickAccess() {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(QuickaccessComponent);
    this.quickAccess.clear();
    const dyynamicComponent = this.quickAccess.createComponent(componentFactory);
  }
  createForm() {
    this.storeLedgerReportForm = this.fb.group({
      'date_from': [null, Validators.required],
      'date_to': [null, Validators.required],
    });
  }

  stockDatas: any = [];

  saveBtn(post, type: boolean) {
    this.stockDatas = [];
    if (!type) {
      this.showGenerateReportButton = true;
      this.stockDatas = [];
    }
    if (post['stock_report_generate']) {
      delete post['stock_report_generate'];
    }
    post['limit'] = 'all';
    post['stock_report'] = true;
    if (type) {
      post['stock_report_generate'] = true;
    }

    if (this.stockId) {
      post['store_id'] = this.stockId;
    }

    this.jqxLoader.open();
    this.rs.getStoreLedgerReport(post, type).subscribe(result => {
      if (type) {
        this.fes.SaveFile(result, 'Stock  Report');
      } else {
        this.reportDatas = result;
        let data: any = result;
        let k = 0;
        for (let i = 0; i < data.category.length; i++) {
          for (let j = 0; j < data.items[i].length; j++) {
            let catName: string = '';
            if (j == 0) {
              catName = data.category[i].name
            }
            let oq = data.opening[k][0].total && parseFloat(data.opening[k][0].total) || 0;
            let iq = data.inward[k][0].total && parseFloat(data.inward[k][0].total) || 0;
            let ouq = data.outward[k][0].total && parseFloat(data.outward[k][0].total) || 0;

            let balance_total = oq + iq - ouq;
            let balance_quantity = data.opening[k][0].quantity + data.inward[k][0].quantity - data.outward[k][0].quantity;
            let balance_rate = balance_total / balance_quantity;

            let rowData = {
              category_name: catName,
              itemname: data.items[i][j].name,
              opening_quantity: data.opening[k][0].quantity,
              opening_total: oq,
              inward_quantity: data.inward[k][0].quantity,
              inward_total: iq,
              outward_quantity: data.outward[k][0].quantity,
              outward_total: ouq,
              balance_total: balance_total,
              balance_quantity: balance_quantity,
              balance_rate: balance_rate
            }
            this.stockDatas.push(rowData);
            k++;
          }
        }
        console.log(this.stockDatas);
      }
      this.jqxLoader.close();
      this.sComboBox.val('');
    },
      error => {
        console.log(error);
        this.jqxLoader.close();
      });
  }

  getStockId($event) {
    this.stockId = $event.args.item.originalItem.id;
  }
}



